<template>
  <div id="app">
    <Loading v-show="loading"/>
    <Header :titulo="titulo" />
    <main>
      <div class="container my-4">
        <div class="row">
          <div class="col-lg-8">
            <Perfil v-if="selecionado.name" :dados="selecionado" />
            <div v-else class="aviso">
              <h2>Selecione uma personagem</h2>
            </div>
          </div>
          <div class="col-lg-4">
            <BarraLateral
              @mostrar-perfil="exibirPerfil"
               @carregar-mais="addItems"
              :personagens="personagens"
            />
          </div>
        </div>
      </div>
    </main>
    <footer>
     <div class="container py-2">
       <p>
          Amamos Vue.js
       </p>
     </div>
    </footer>
  </div>
</template>

<script>
import 'bootstrap/dist/css/bootstrap.min.css'
import Header from '@/components/Header'
import BarraLateral from '@/components/BarraLateral'
import Perfil from '@/components/Perfil'
import Loading from '@/components/Loading'
import axios from 'axios'
export default {
  name: 'marvel-api',
  data() {
    return {
      personagens: [],
      selecionado: {},
      titulo: 'Marvel API',
      apiMarvel: {
        publicKey: '3f9accccdbd7b49e7dd8229768e90be4',
        // privateKey: 'e101eba9e32de119a6b3f897466d26f8f8efc8b0',
      },
      loading: true,
      offset: 0
    }
  },
  components: {
    Header,
    BarraLateral,
    Perfil,
    Loading
  },
  methods: {
    async obterPersonagens() {
      try {
        const response = await axios(
          `https://gateway.marvel.com:443/v1/public/characters?orderBy=name&limit=30&offset=${this.offset}&apikey=${this.apiMarvel.publicKey}`
        ).then(r => r.data.data.results)
        this.personagens = response
        this.loading = false
      } catch (e) {
        console.log(e)
      }
    },
    exibirPerfil(payload) {
      this.selecionado = payload
    },
    async addItems(){
      this.offset += 30
      this.loading = true
      await this.obterPersonagens()
    }
  },
  async created() {
    await this.obterPersonagens()
  },
}
</script>

